.answer {
  padding: 10px;
  background-color: #fca25d;
  color: white;
  transition: 70ms;
  margin: 25px;
}

.invalid {
  padding: 10px;
  background-color: rgba(85, 85, 85, 0.75);
  color: white;
  transition: 70ms;
  margin: 25px;
}

.invalid:active {
  background-color: rgba(85, 85, 85, 0.75);
}

.answer:active {
  background-color: #AFD9EA;
}

.selectedAnswer{
  padding: 10px;
  background-color: #AFD9EA;
  color: white;
  transition: 70ms;
  margin: 25px;
}